<template>
  <div class="info">
    <h1>profile</h1>
    <transition
      :duration="{ enter: 2500, leave: 1500 }"
      enter-active-class="animated fadeInRight">
      <div class="cont"  v-if="show">
        <swiper :options="swiperOption" >
          <!-- slides -->
          <swiper-slide class="con" v-for="(item,index) in profiles" :key="index">
            <div class="item">
              <h2 v-text="item.name"></h2>
              <p v-text="'技术栈: ' + item.skills"></p>
              <p v-text="item.content"></p>
              <a :href="item.github">
                <p v-text="'github地址: ' + item.github"></p>
              </a>

            </div>
          </swiper-slide>
          <!-- Optional controls -->
          <div class="swiper-pagination"  slot="pagination"></div>
          <!--<div class="swiper-button-prev" slot="button-prev"></div>-->
          <!--<div class="swiper-button-next" slot="button-next"></div>-->
          <div class="swiper-scrollbar"   slot="scrollbar"></div>
        </swiper>
      </div>
    </transition>
  </div>
</template>
<script>
import {swiper, swiperSlide} from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
export default {
  data () {
    return {
      show: false,
      profiles: [
        {
          name: 'takeout',
          skills: 'vue-cli vue-router axios vuex mint-ui swiper vue-lazyload vue-scroller date-fns mock es6',
          content: '此项目为一个前后端分离的外卖 Web App (SPA) 项目，使用了 Vue 全家桶+ES6+Webpack 等前端最新技术,包括商家, 商品, 购物车, 用户等多个功能子模块，采用了模块化、组件化、工程化的模式开发',
          github: 'https://github.com/shunxuanyou/Vue-takeout'
        },
        {
          name: '光谷招聘(client)',
          skills: 'react-create-app react-router-dom redux antd-mobile async/await',
          content: '这是一个前后台分离的招聘的 SPA, 包括前端应用和后端应用，包括用户注册/登陆, 大神/老板列表, 实时聊天等模块，使用 React 全家桶+ES6+Webpack 等技术，后端使用 Node + express + mongodb + socketIO 等技术',
          github: 'https://github.com/shunxuanyou/react-recruit'
        },
        {
          name: '光谷招聘(server)',
          skills: 'node express mongodb mongoose socket.io',
          content: '这是一个代理招聘项目的服务端，使用express框架，选择的数据库为mongodb，使用mongoose操作数据库，接口设计的是比较简单的登录、注册、更新用户信息、获取用户列表、获取当前用户的聊天消息列表等',
          github: 'https://github.com/shunxuanyou/react-recruit'
        },
        {
          name: 'qq-music',
          skills: 'vue-cli vue-router vuex vue-lazyload better-scroll jsonp axios ES6 ',
          content: '此项目是一个前端应用，使用 jsonp 抓取 QQ音乐(移动端)数据，包括歌单详情页、歌手详情页、搜索页等，采用了模块化、组件化的开发模式',
          github: 'https://shunxuanyou.github.io/dist/index.html#/recommend'
        }
      ],
      swiperOption: {
        pagination: {
          el: '.swiper-pagination'
        },
        autoplay: true,
        delay: 2000
      }
    }
  },
  methods: {
    timeout () {
      setTimeout(() => {
        this.show = true
      }, 1000)
    }
  },
  mounted () {
    this.timeout()
  },
  components: {
    swiper,
    swiperSlide
  }

}
</script>

<style lang="less" scoped>
@import '../less/index.less';
.info {
  .info;
  .cont {
    .cont;
    width: 90%;
    .con{
      height: 500px;
      margin: 40px auto;
      background-color: rgba(233, 225, 225, .3);
      border-radius: 25px;
    }
    .item {
      background-color: rgba(59, 123, 13, 0.2);
      padding: 10px;
      border-radius: 15px;
      position: relative;
      top: 32%;
      @media screen and (max-width: 768px) {
        padding: 5px;
        margin: 5px auto;
      }
      &:hover {
        border-radius: 25px;
        background-color: rgba(10, 135, 87, 0.3);
      }
      p {
        text-align: left;
        text-indent: 28px;
        font-size: 1.1rem;
        line-height: 1.2;
        @media screen and (max-width: 768px) {
          text-indent: 12px;
          font-size: 0.7rem;
          line-height: 1.1;
        }
      }
    }
  }
}
</style>



