import Vue from 'vue'
// 全局使用element-ui
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
// 引入animate动画库
import animate from 'animate.css'
import App from './App'

Vue.config.productionTip = false
Vue.use(ElementUI)
Vue.use(animate)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>'
})



